// В этом файле должны быть стили для БЭМ-блока field-text, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

$border-color:                $color-main;

$text-color--muted:           hsl(0, 0%, 50%) !default;

$line-height:                 1.5 !default;

$field-padding-vertical:      0.7em !default;
$field-padding-horizontal:    0.7em !default;

$border-radius:               2px;

.field-text {
  $c: &;

  $block-name:                &; // #{$block-name}__element

  margin-bottom: 1rem;

  & > * {
    width: 100%;
  }

  &__name {
    order: 0;
    @include field-name;
  }

  &__input-wrap {
    @include dflex;
  }

  &__input {
    order: 1;

    display: inline-block;
    vertical-align: middle;
    width: 100%;
    // height: 2.125em;
    padding: 15px 20px;
    margin: 0;
    line-height: $line-height;
    font-size: 1.25em;
    font-family: inherit;
    border: 2px solid #ECECEC;
    border-radius: $border-radius;
    appearance: textfield;

    &:focus {
      @include field-focus;
    }

    &[disabled],
    &[readonly] {
      cursor: default;
      background-color: white;
    }

    &[type='search']::-webkit-search-cancel-button,
    &[type='search']::-webkit-search-decoration {
      -webkit-appearance: none; // stylelint-disable-line  property-no-vendor-prefix
    }

    &[type='number']::-webkit-inner-spin-button,
    &[type='number']::-webkit-outer-spin-button {
      // height: auto;
    }

    &[type='color'] {
      // max-width: 100px;
      padding: 0;
    }

    &::placeholder {
      color: $text-color--muted;
      opacity: 1;
    }

    @at-root textarea#{&} {
      $textarea-height: ($line-height * 2) + ($field-padding-vertical * 2);

      height: auto;
      min-height: calc(#{$textarea-height} + 2px);
      resize: vertical;
    }
  }

  &__help-text {
    @include field-help-text;
  }

  &--animated {
    & #{$c}__name {
      transform: translateY(195%) translateX(20px);
      transition: transform 0.5s;
    }

    & #{$c}__input:focus + #{$c}__name {
      transform: translateY(0) translateX(0);
    }
  }

  &--color-white {
    & #{$c}__name {
      color: #fff;
    }

    & #{$c}__input {
      color: white;
    }
  }


  &--bg-hollow {
    & #{$c}__input {
      background-color: transparent;
    }
  }
}
