// В этом файле должны быть стили для БЭМ-блока btn, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

$gray-lighter:                hsl(0, 0%, 80%) !default;
$gray-lightest:               hsl(0, 0%, 90%) !default;
$text-color:                  hsl(0, 0%, 10%) !default;
$line-height:                 1.5 !default;


$border-radius:               rem(3px) !default;

.btn {
  $c: &;

  $block-name:                &; // #{$block-name}__element
  min-width: 110px;
  box-sizing: border-box;
  display: inline-block;
  vertical-align: middle;

  margin: 0;

  padding: 12px 20px;


  border-radius: $border-radius;
  border: 1px solid $gray-lighter;

  background-color: $gray-lightest;
  background-image: none;

  cursor: pointer;

  white-space: nowrap;
  user-select: none;
  text-align: center;
  font-size: 1em;
  font-family: inherit;
  line-height: $line-height;
  text-decoration: none;
  text-transform: none;
  color: $text-color;

  transition: transform .3s;

  &:hover,
  &:focus {
    text-decoration: none;
    color: $text-color;
    background-color: darken($gray-lightest, 10%);
    border-color: darken($gray-lighter, 12%);
  }

  &:visited {
    color: $text-color;
  }

  &:active,
  &:focus {
    @include field-focus;
  }

  &--disabled,
  &:disabled {
    opacity: $opacity;
    cursor: not-allowed;
    color: $text-color;
    background-color: $gray-lightest;
    border-color: $gray-lighter;

    @at-root a.btn--disabled {
      pointer-events: none;
    }
  }

  // &__icon {
  //   display: inline-block;
  //   vertical-align: middle;
  // }



  // &--main {
  //   color: #fff;
  //   background-color: $color-main;
  //   border: 1px solid $color-main;

  //   &:hover,
  //   &:focus {
  //     color: #fff;
  //     background-color: darken($color-main, 10%);
  //     border-color: darken($color-main, 12%);
  //   }

  //   &--disabled,
  //   &:disabled {
  //     color: #fff;
  //     background-color: $color-main;
  //     border-color: $color-main;
  //   }
  // }

  &--candy {
    @include colorizebtn($color-main, white);
  }

  &--color-white {
    background-color: #fff;
    color: $color-main;
    border-color: #fff;


    &:hover {
      background-color: lighten($color-main,50%);
      border-color: lighten($color-main,50%);

      transform: translateY(-3px);

      & #{$c}__text {
        color: $color-main;
      }
    }
  }

  &--size-medium {
    min-width: 244px;

    & #{$c}__text {
      text-transform: uppercase;

      padding: 17px;

      font-size: 18px;
    }
  }
}
