// В этом файле должны быть стили для БЭМ-блока table, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

$text-color--muted:           hsl(0, 0%, 50%) !default;

$border-color:                hsl(0, 0%, 60%) !default;

$font-size--small:            80% !default;

$field-padding-vertical:      0.3em !default;
$field-padding-horizontal:    0.7em !default;

.table {

  $block-name:                &; // #{$block-name}__element

  width: 100%;
  table-layout: fixed;
  min-width: 420px;
  margin-bottom: 0;

  &__head, &__body {
    width: 100%;
  }

  &__th {
    width: 30%;

    font-weight: bold;

    padding-top: 16px;
    padding-bottom: 16px;
    padding-left: 20px;
    padding-right: 20px;

    &:last-child .table__label {
      justify-content: flex-end;
    }

    @include for-tablet-portrait-up {
      &:first-child {
        width: 20%;
      }

      &:last-child {
        width: 50%;
      }
    }
  }

  &__td {
    padding-top: 16px;
    padding-bottom: 16px;
    padding-left: 20px;
    padding-right: 20px;

    &:last-child {
      & .balance__list {
      justify-content: flex-end;
      }
    }
  }

  &__body &__tr {
    &:nth-child(2n+1) {
      background-color: transparentize(#EA064A, .95);
    }
  }

  &__label {
    @include dflex;
    flex-wrap: nowrap;
  }

  &__icon {
    @include dflex;
    align-items: center;

    padding-left: 5px;
    padding-right: 5px;
  }

  &__value {
    @include dflex;
  }
}

.much-more {
  transform: translateY(-230%);
  margin-left: 40px;

  @include for-tablet-portrait-up {
    margin-left:auto;
    transform: none;
  }
}
