// В этом файле должны быть стили для БЭМ-блока logo, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.logo {
  $block-name:                &; // #{$block-name}__element

  display: inline-block;
  white-space: nowrap;
  height: 44px;


  &:hover {
    #{$block-name}__m {
      transform: translate(24845.7px, -991.812px);
    }

    #{$block-name}__y {
      opacity: 1;
    }

    #{$block-name}__t {
      opacity: 1;

      transform: translate(24911.2px, -1009.6px);

      transition-delay: 0.3s;
    }

    #{$block-name}__h {
      opacity: 1;

      transform: translate(24911.2px, -1009.6px);

      transition-delay: 0.8s;
    }
  }

  img {
    display: block;
  }

  &__t {
    opacity: 0;

    transform: translate(24881.2px, -1009.6px);

    transition: 0.3s ease;
    transition-delay: 0.4s;
  }

  &__h {
    opacity: 0;

    transform: translate(24861.2px, -1009.6px);


    transition: 0.3s ease;

    transition-delay: 0.2s;
  }

  &__m {
    transform: translate(24850.7px, -991.812px) scale(1.5);
    fill: $color-main;

    transform-origin: 0 15%;

    transition: 0.3s ease;
  }

  &__y {
    fill: $color-main;

    opacity: 0;

    transform: translate(24845.7px, -991.812px);

    transition: 0.3s ease;
  }

  &__desktop {
    display: none;

    @include for-desktop-up {
      display: block;
    }
  }

  &__mobile {
    display: block;

    @include for-desktop-up {
      display: none;
    }
  }
}
