
// Сделаем viewport великим снова! (привет, IE10)
// http://timkadlec.com/2012/10/ie10-snap-mode-and-responsive-design/
@at-root {

  @-ms-viewport {
    width: device-width;
  }
}


*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  // Типографика проекта — в блоке typo
  display: flex;           // Исправляем баг в IE для min-height and flexbox (flex-direction:column)
  flex-direction: column;  // и прижимаем footer в IE 10-11
  background-color: transparent;
  margin: 0;
  min-height: 100%;
}

[tabindex='-1']:focus {
  outline: none !important;
}

// Коррекция для кликабельных элементов с указанной ролью в iOS
[role='button'] {
  cursor: pointer;
}

// Мы любим Сафари (на самом деле, нет).
input[type='search'] {
  -webkit-appearance: textfield;
}

// area,
// summary,
a,
button,
[role='button'],
input,
label,
select,
textarea {
  touch-action: manipulation; // Стилевая коррекция 300ms задержки (для поддерживающих браузеров)
  margin: 0;                  // Убираем margin в Firefox и Safari
}

button,
input {
  overflow: visible;    // Покажем торчащее в Edge
  text-transform: none; // Уберём наследование text transform в Firefox
}

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button; // Коррекция проблем стилизации для Safari
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  -webkit-appearance: listbox; // Коррекция для Mobile Safari (https://github.com/twbs/bootstrap/issues/11266)
}

textarea {
  overflow: auto;   // Удаление верт. скролла в IE.
  resize: vertical; // Правильное изменение размеров
}

svg {

  a &,
  button & {
    pointer-events: none; // Мы любим Edge, это браузер! (почти)
  }

  &:not(:root) {
    overflow: hidden; // Скрыть выступающее за границы в IE
  }
}

img {
  max-width: 100%;
  height: auto;
}

a {
  color: $link-color;
  background-color: transparent;
  text-decoration-skip: ink;
  -webkit-text-decoration-skip: objects;
  transition: color 0.3s;

  &:hover,
  &:focus {
    color: $link-color--hover;
  }
}

hr {
  box-sizing: content-box; // Проблема Firefox
  overflow: visible;       // Проблема Edge и IE
}


.page { // html

  $block-name:                &; // #{$block-name}__element

  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -ms-overflow-style: scrollbar;
  -ms-text-size-adjust: 100%;
  min-width: 320px;
  min-height: 100%;

  background-image: url('../img/bg.svg');
  background-position: center 0;
  background-repeat-x: repeat;

  &__inner {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
  }

  &__content {
    flex-grow: 1;
  }

  &__footer-wrapper {
    flex-shrink: 0;
  }

  &__title-area {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  &__field-container {
    max-width: 557px;
    width: 100%;

    margin-left:auto;
    margin-right:auto;

    padding-top: 0;
    padding-bottom: 20px;

    @include for-tablet-landscape-up {
      padding-top: 20px;
    }
  }

  &__table {
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;

    box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.04);

    background-color: #fff;

    margin-bottom: 1rem;

    overflow-x: auto;
  }

  &__balances {
    display: flex;
    align-items: center;
    justify-content: center;

    padding-top: 10px;
    padding-bottom: 10px;

    @include for-tablet-portrait-up {
      padding-top: 20px;
      padding-bottom: 20px;
    }

    & > * {
      width: 100%;
    }
  }

  &__section--cutted-edges {
    position: relative;
    z-index: 0;
  }

  &__section {
    min-height: 500px;
  }
}

.section {
  $s: &;
  position: relative;
  z-index: 1;

  &__inner {
    padding-left: 20px;
    padding-right: 20px;
  }

  &--cutted-edges {
    margin-top: -80px;

    //transform: skewY(-6deg);
    //
    //& > *{
    //  transform: skewY(6deg);
    //}
  }

  &--bg-velvet {
    position: relative;
    background-image:
            url('../img/bg-triangles.svg'),
            linear-gradient(7.93deg, #EA064A 25.43%, #260C69 93.36%);

    background-repeat: no-repeat;

    background-position: center -160px, 0 0;

    color: #fff;

    padding-top: 120px;

    & a {
      color: #fff;
    }

    &:after {
      content:'';
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      z-index: -1;
      width: 100%;
      height: 100%;

      transform: translateY(-7%) skewY(-6deg);

      background-image:
              url('../img/bg-triangles.svg'),
              linear-gradient(7.93deg, #EA064A 25.43%, #260C69 93.36%);
    }
  }
}

.balance {
  width: 100%;
  &__list {
    @include reset-ul;
    list-style-type: none;
    @include dflex;
    justify-content: center;
  }

  &__item {
    display: flex;
    align-items: center;

    position: relative;

    width: 100%;

    @include for-tablet-portrait-up {
      padding-right: 50px;

      width: auto;

      &:after {
        content:'=';
        position: absolute;

        font-size: 18px;
        color: #A6A6A6;

        right: 20px;
        top: 0;
        line-height: normal;
        height: 100%;
      }

      &:last-child {
        padding-right: 0;

        &:after {
          display: none;
        }
      }
    }
  }

  &__currency {
    text-transform: uppercase;
    color: #EA064A;
    padding-right: 5px;
    padding-left: 5px;
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    padding-right: 5px;
    padding-left: 5px;
  }
}

.icon {
  padding-right: 5px;
  padding-left: 5px;

  display: inline-block;
  vertical-align: middle;
  width: 35px;

  & > * {
    width: 100%;
  }
}

.link {
  text-decoration: none;

  &__text {
    border-bottom: 1px solid;
  }

  &--white {
    color: #fff;

    & > * {
      color: #fff;
      fill: white;
    }
  }
}

.button-container {
  @include dflex;
  justify-content: center;
  align-items: center;

  padding-top: 40px;
  padding-bottom: 40px;
}

.roadmap {
  padding-top: 52px;
  padding-bottom: 14px;

  &__inner {
    margin-top: 32px;
    position: relative;
  }

  &__current-point {
    display: none;
    position: absolute;
    top: calc(25% - 7px);
    right: calc(50% + 3px);

    @include for-tablet-portrait-up {
      display: flex;
    }
  }

  &__pointer-text {
    display: none;

    @include for-tablet-portrait-up {
      display: inline-block;
      text-transform: uppercase;
      width: 70px;
      line-height: 90%;
      font-weight: bold;
      text-align: center;
      color: #ADADFF;
      font-size: 18px;
      margin-right: 10px;
    }
  }

  &__item {
    position: relative;

    width: 100%;

    max-width: 300px;

    min-height: 76px;
    padding-bottom: 24px;

    transform: translateX(40px);

    color: transparentize(white,.2);

    &:after, &:before {
      content:'';
      display: none;
      width: 16px;
      height: 16px;

      border-radius: 16px;

      background-color: #D9064D;
      border: 2px solid (transparentize(white, .5));

      position: absolute;
      top: 6px;
    }

    &:before {
      display: block;
      right: calc(100% + 32px);
    }

    @include for-tablet-portrait-up {

      margin-left: auto;
      margin-right: auto;

      transform: translateX(calc(50% + 40px));

      &:nth-child(2n+2) {
        text-align: right;
        transform: translateX(calc(-50% - 40px));

        &:before {
          display: none;
        }

        &:after {
          display: block;
          left: calc(100% + 32px);
        }
      }
    }

    &.finished {
      color: white;

      &:after, &:before {
        background-color: $color-main;
        border: 2px solid white;
      }
    }

    @include for-tablet-portrait-up {
      max-width: calc(50% - 40px);
    }

    @include for-tablet-landscape-up {
      max-width: 300px;
    }
  }

  &__title {
    font-size: 18px;
    font-weight: bold;
    letter-spacing: 1px;
  }

  &__text {
    margin-top: 4px;

    font-size: 14px;
  }

  &__line {
    width: 2px;
    height: 100%;
    position: absolute;
    z-index: 0;
    top: 6px;
    left: 0;
    background-color: transparentize(#fff,.5);


    &:after {
      content:'';
      display: block;
      width: 100%;
      height: 25%;
      background-color: #fff;
      position: absolute;
      top: 0;
      left: 0;
    }

    @include for-tablet-portrait-up {
      left: calc(50% - 1px);
    }
  }

  &__pommel {
    position: absolute;
    top: calc(100% + 5px);
    left: calc(0% - 15px);;
    opacity: .5;

    @include for-tablet-portrait-up {
      left: calc(50% - 15px);
    }
  }

  &__more {
    width: 100%;
  }
}

.socials {
  max-width: 400px;

  padding-top: 20%;

  padding-left: 36px;
  padding-right: 36px;

  background-image: url('../img/safe.svg');

  margin-left:auto;
  margin-right:auto;

  margin-top: 32px;

  background-size: contain;
  background-repeat: no-repeat;
  background-position: center bottom;

  @include for-tablet-portrait-up {
    background-size: auto;

    padding-left: 72px;
    padding-right: 72px;
  }

  &__list {
    @include dflex;
    @include reset-ul;

    justify-content: space-between;

    max-width: 280px;
    margin-left: auto;
    margin-right: auto;
  }

  &__title {
    font-size: 18px;
    margin-bottom: 20px;
  }


  &__item {

  }

  &__link {
    display: inline-block;
    transition: transform .3s;

    &:hover {
      transform: translateY(-3px);
    }
  }
}
